<template>
  <div style="width: 1200px;">
    <Row>
      <Col span="24">
        <span>{{textDic.text0}}</span>
        <Date-picker type="date"
                @on-change="handleChangeFrom"
                format="yyyy年MM月dd日" 
                :placeholder="textDic.text1" 
                style="width: 200px"></Date-picker>
        <span>{{textDic.text2}}</span>
        <Date-picker type="date"
                v-model="value1"
                @on-change="handleChangeEnd"
                format="yyyy年MM月dd日" 
                :placeholder="textDic.text3"
                style="width: 200px"></Date-picker>
        <span style="margin-left: 5%">{{textDic.text4}}</span>
        <select v-model="formData.value2" style="width:200px">
          <option :value="textDic.text6">{{textDic.text6}}</option>
          <option :value="textDic.text7">{{textDic.text7}}</option>
        </select>
        <span style="margin-left: 5%">{{textDic.text8}}</span>
        <Input v-model="formData.value3" :placeholder="textDic.text8" style="width: 200px"/>
      </Col>
    </Row>
    <Row>
      <Col span="24">
        <div class="addBtn">
          <Button type="primary" @click="didClickAdd">{{textDic.text9}}</Button>
        </div>
        <table border="1px" cellspacing="0" cellpadding="0">
          <tr>
            <th v-for="(title, i) in textDic.tableTitles" :key="i">{{title}}</th>
          </tr>
          <tr>
            <td v-for="(text, j) in formItem" :key="j">{{text}}</td>
          </tr>
        </table>
      </Col>
    </Row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formData: {
        value0: '', // 开始日期
        value1: '', // 结束日期
        value2: '', // 状态
        value3: '', // 手机号
      },
      formItem: []
    }
  },
  methods: {
    formItems() {
      var arr = [this.textDic.tableTitles];
      if(this.formItem.length>0){
        arr.concat(this.formItem);
      }
      return arr;
    },
    tableText(i, j, text) {
      // 如果是第一行，说明是标题，直接返回
      if(i == 0) {
        return text;
      }
      /**
       * 第一行是日期
       * 第二行是账号类型
       * 第三行是手机号
       * 第四行是状态
       * 第五行是操作
       * */
      switch(j){
        case 0:
          return text;
          break;
        case 1:
          return text;
          break;
        case 2: 
          return text;
          break;
        case 3:
          return text;
          break;
        default:
        return text;
      }
    },
    handleChangeFrom(date) {
      this.formData.value0 = date
    },
    handleChangeEnd(date) {
      this.formData.value1 = date
    },
    didClickAdd() {

    }
  },
  computed:{
    textDic() {
      return {
        text0: '添加日期',
        text1: '开始日期',
        text2: '---',
        text3: '结束日期',
        text4: '状态',
        text5: '请选择',
        text6: '正常',
        text7: '停用',
        text8: '手机号',
        text9: '添加',
        tableTitles: ['添加日期', '账号类型', '手机号', '状态', '操作']
      }
    }
  }
}
</script>

<style>
  .addBtn {
    width: 1092px; 
    background-color: #333;
    margin-top: 60px;
    padding: 5px 10px;;
  }
  td, th {
    width: 218px; 
    height: 60px;
  }
</style>



